body {
  background: #FFF;
  line-height: 1.4;
}

.block {
  display: flex;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 230px;
  background-color: #37474f;
  font-family: "Open Sans", sans-serif;
}

.sidebar__title {
  background-color: #1e88e5;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px;
}

.sidebar-menu__item {
  display: flex;
  align-items: center;
  padding: 20px 22px;
  opacity: 0.5;
  transition: background-color 0.4s, opacity 0.4s;
}

.no-touch .sidebar-menu__item:hover {
  background-color: #455a64;
  opacity: 1;
}

.sidebar-menu__link {
  text-decoration: none;
  color: #fff;
}

.sidebar-menu__icon {
  color: #fff;
  margin-right: 16px;
  font-size: 0;
  display: inline-block;
  vertical-align: middle;
}

.sidebar-menu__icon .icon {
  font-size: 16px;
  max-width: 16px;
}

.sidebar-menu__text {
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
}

.main {
  float: right;
  height: 100vh;
  width: calc(100% - 230px);
  background-color: #f5f5f5;
}

@media only screen and (max-width: 767px) {
  .main {
    float: none;
    width: 100%;
  }
}

.main__inner {
  padding: 0 72px;
}

@media only screen and (max-width: 767px) {
  .main__inner {
    padding: 0 15px;
  }
}

.main__bottom-charts {
  display: flex;
  justify-content: space-between;
}

.area-chart-wrapper {
  padding: 20px;
  background-color: #fff;
  margin-bottom: 40px;
}

.donat-chart-wrapper {
  background-color: #fff;
  padding: 18px;
  flex: 0 0 40%;
  min-width: 40%;
}

.donat-chart-wrapper canvas {
  width: 100%;
}

.bar-chart-wrapper {
  background-color: #fff;
  padding: 15px;
  flex: 0 0 calc(60% - 12px);
  min-width: calc(60% - 12px);
}

.bar-chart-wrapper canvas {
  width: 100%;
}
